  color: var(--wp--preset--color--base);
  padding: 10px 15px;
  background: var(--wp--preset--color--tertiary);
  border-radius: 0;
  transition: all 0.2s ease-in-out;
  align-items: center;
  justify-content: center;
}
.vacature-navigatie a.job-button span:first-of-type {
  font-size: 30px;
}
.vacature-navigatie a.job-button:hover {
  background-color: var(--wp--preset--color--primary);
  color: #fff;
}
.vacature-navigatie a.search-button div {
  flex-flow: column;
  text-align: center;
}
.vacature-navigatie a.search-button div span:first-of-type {
  font-size: 32px;
}
.vacature-navigatie a.search-button div.open {
  display: none;
}
.vacature-navigatie a.search-button div.closed {
  display: flex;
}
.vacature-navigatie a.search-button.open div.open {
  display: flex;
}
.vacature-navigatie a.search-button.open div.closed {
  display: none;
}
.vacature-navigatie a .teller {
  display: inline-block;
  width: 22px;
  height: 22px;
  line-height: 22px;
  text-align: center;
  border-radius: 50%;
  background-color: var(--wp--preset--color--tertiary);
  color: #fff;
  font-size: 14px;
  position: absolute;
  top: 0;
  right: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 300ms, opacity 300ms;
}
.vacature-navigatie a .teller.show {
  visibility: visible;
  opacity: 1;
}
.vacature-navigatie a .onderschrift {
  font-size: 14px;
}

#jobalert-holder {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100vw;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 1001;
  display: none;
}
#jobalert-holder.show {
  display: block;
}
#jobalert-holder .jobalert {
  margin: 30px auto;
  max-width: 1200px;
  width: calc(100vw - 6rem);
  height: auto;
  max-height: 800px;
  background-color: #fff;
  padding: 20px;
  overflow: scroll;
  position: relative;
}
#jobalert-holder .jobalert .jobalert-button {
  background: none;
  border: none;
  color: var(--wp--preset--color--primary);
  font-size: 2rem;
  position: absolute;
  right: 20px;
}